import React, { useEffect, useRef } from 'react';
import * as d3 from 'd3';

interface ImpactAnalysisProps {
  nodeId: string;
  nodeName: string;
  nodeType: string;
  connections: {
    incoming: Array<{
      source_id: string;
      source_name: string;
      source_type: string;
      target_id: string;
      target_name: string;
      target_type: string;
      type: string;
    }>;
    outgoing: Array<{
      source_id: string;
      source_name: string;
      source_type: string;
      target_id: string;
      target_name: string;
      target_type: string;
      type: string;
    }>;
  };
  hasIssues: boolean;
  highComplexity: boolean;
}

const ImpactAnalysis: React.FC<ImpactAnalysisProps> = ({
  nodeId,
  nodeName,
  nodeType,
  connections,
  hasIssues,
  highComplexity,
}) => {
  const svgRef = useRef<SVGSVGElement>(null);

  useEffect(() => {
    console.log('DEBUG - Starting useEffect for node:', nodeId);
    if (!svgRef.current) {
      console.error('DEBUG - SVG ref is null, aborting');
      return;
    }

    console.log('DEBUG - Incoming connections:', JSON.stringify(connections.incoming));
    console.log('DEBUG - Outgoing connections:', JSON.stringify(connections.outgoing));

    d3.select(svgRef.current).selectAll('*').remove();
    console.log('DEBUG - SVG cleared');

    const width = 600;
    const height = 400;

    const svg = d3.select(svgRef.current)
      .attr('width', width)
      .attr('height', height);
    console.log('DEBUG - SVG initialized with width:', width, 'height:', height);

    const allNodes: any[] = [];
    const allLinks: any[] = [];

    allNodes.push({
      id: nodeId,
      name: nodeName,
      type: nodeType,
      level: 0,
      isCenter: true,
      hasIssues,
      highComplexity,
      x: width / 2,
      y: height / 2
    });
    console.log('DEBUG - Center node added:', { id: nodeId, name: nodeName });

    const addedNodeIds = new Set([nodeId]);

    if (connections.incoming && Array.isArray(connections.incoming)) {
      const validIncoming = connections.incoming.filter(conn => {
        if (!conn.source_id || !conn.source_name || !conn.target_id || !conn.target_name) {
          console.error('DEBUG - Invalid incoming connection:', conn);
          return false;
        }
        return true;
      });
      console.log('DEBUG - Valid incoming connections:', validIncoming.length);

      validIncoming.forEach((conn, index) => {
        const sourceId = conn.source_id;
        const sourceName = conn.source_name;
        const sourceType = conn.source_type;

        if (!addedNodeIds.has(sourceId)) {
          const angle = (Math.PI / (validIncoming.length + 1)) * (index + 1);
          const radius = 150;
          const x = width / 2 - radius * Math.cos(angle);
          const y = height / 2 - radius * Math.sin(angle);

          allNodes.push({
            id: sourceId,
            name: sourceName,
            type: sourceType,
            level: 1,
            direction: 'incoming',
            x: x,
            y: y
          });
          addedNodeIds.add(sourceId);
          console.log('DEBUG - Incoming node added:', { id: sourceId, name: sourceName });
        }

        allLinks.push({
          source: sourceId,
          target: nodeId,
          type: conn.type || 'unknown',
          direction: 'incoming'
        });
        console.log('DEBUG - Incoming link added:', { source: sourceId, target: nodeId });
      });
    } else {
      console.warn('DEBUG - No valid incoming connections provided');
    }

    if (connections.outgoing && Array.isArray(connections.outgoing)) {
      const validOutgoing = connections.outgoing.filter(conn => {
        if (!conn.source_id || !conn.source_name || !conn.target_id || !conn.target_name) {
          console.error('DEBUG - Invalid outgoing connection:', conn);
          return false;
        }
        return true;
      });
      console.log('DEBUG - Valid outgoing connections:', validOutgoing.length);

      validOutgoing.forEach((conn, index) => {
        const targetId = conn.target_id;
        const targetName = conn.target_name;
        const targetType = conn.target_type;

        if (!addedNodeIds.has(targetId)) {
          const angle = (Math.PI / (validOutgoing.length + 1)) * (index + 1);
          const radius = 150;
          const x = width / 2 + radius * Math.cos(angle);
          const y = height / 2 - radius * Math.sin(angle);

          allNodes.push({
            id: targetId,
            name: targetName,
            type: targetType,
            level: 1,
            direction: 'outgoing',
            x: x,
            y: y
          });
          addedNodeIds.add(targetId);
          console.log('DEBUG - Outgoing node added:', { id: targetId, name: targetName });
        }

        allLinks.push({
          source: nodeId,
          target: targetId,
          type: conn.type || 'unknown',
          direction: 'outgoing'
        });
        console.log('DEBUG - Outgoing link added:', { source: nodeId, target: targetId });
      });
    } else {
      console.warn('DEBUG - No valid outgoing connections provided');
    }

    console.log('DEBUG - All nodes:', allNodes.map(n => ({ id: n.id, name: n.name })));
    console.log('DEBUG - All links:', allLinks.map(l => ({ source: l.source, target: l.target, type: l.type })));

    const impactScores = new Map<string, number>();
    impactScores.set(nodeId, 10);

    allNodes.forEach(node => {
      if (node.id === nodeId) return;

      let score = 5;

      const directConnections = allLinks.filter(link =>
        String(link.source) === String(node.id) || String(link.target) === String(node.id)
      );

      directConnections.forEach(link => {
        if (link.type === 'contains') score += 3;
        else if (link.type === 'imports') score += 2;
        else if (link.type === 'calls') score += 1;
      });

      if (node.type === 'module') score += 3;
      else if (node.type === 'class') score += 2;
      else if (node.type === 'function' || node.type === 'method') score += 1;

      score = Math.min(10, Math.max(0, score));
      impactScores.set(node.id, score);
      console.log('DEBUG - Impact score calculated for', node.id, ':', score);
    });

    const defs = svg.append('defs');
    ['imports', 'contains', 'calls', 'unknown'].forEach(type => {
      defs.append('marker')
        .attr('id', `impact-arrow-${type}`)
        .attr('viewBox', '0 -5 10 10')
        .attr('refX', 20)
        .attr('refY', 0)
        .attr('markerWidth', 6)
        .attr('markerHeight', 6)
        .attr('orient', 'auto')
        .append('path')
        .attr('fill', type === 'imports' ? '#7c3aed' : type === 'contains' ? '#059669' : type === 'calls' ? '#ea580c' : '#888888')
        .attr('d', 'M0,-5L10,0L0,5');
    });
    console.log('DEBUG - Arrow markers defined');

    svg.append('text')
      .attr('x', 10)
      .attr('y', 380)
      .attr('fill', '#666')
      .attr('font-size', '12px')
      .text(`Total nodes: ${allNodes.length}, Total links: ${allLinks.length}`);
    console.log('DEBUG - Footer text added');

    const simulation = d3.forceSimulation(allNodes)
      .force('link', d3.forceLink(allLinks).id((d: any) => d.id).distance(120))
      .force('charge', d3.forceManyBody().strength(-600))
      .force('center', d3.forceCenter(width / 2, height / 2))
      .force('collision', d3.forceCollide().radius(40))
      .alphaDecay(0.028);
    console.log('DEBUG - Force simulation initialized');

    const link = svg.append('g')
      .selectAll('path')
      .data(allLinks)
      .enter()
      .append('path')
      .attr('stroke', d => {
        if (d.type === 'imports') return '#7c3aed';
        if (d.type === 'contains') return '#059669';
        if (d.type === 'calls') return '#ea580c';
        return '#888888';
      })
      .attr('stroke-width', d => (d.source === nodeId || d.target === nodeId) ? 2 : 1.5)
      .attr('stroke-opacity', d => (d.source === nodeId || d.target === nodeId) ? 0.8 : 0.6)
      .attr('marker-end', d => `url(#impact-arrow-${d.type})`)
      .attr('fill', 'none');
    console.log('DEBUG - Links rendered:', allLinks.length);

    const node = svg.append('g')
      .selectAll('.node')
      .data(allNodes)
      .enter()
      .append('g')
      .attr('class', 'node')
      .call(d3.drag<any, any>()
        .on('start', dragstarted)
        .on('drag', dragged)
        .on('end', dragended));
    console.log('DEBUG - Nodes rendered:', allNodes.length);

    node.append('circle')
      .attr('r', d => (d.isCenter ? 15 : 10))
      .attr('fill', d => {
        if (d.isCenter) return highComplexity ? '#ef4444' : (hasIssues ? '#f59e0b' : '#3b82f6');
        if (d.type === 'module') return '#93c5fd';
        if (d.type === 'class') return '#67e8f9';
        if (d.type === 'function' || d.type === 'method') return '#6ee7b7';
        return '#d1d5db';
      })
      .attr('stroke', d => {
        if (d.isCenter) return '#3b82f6';
        if (d.type === 'module') return '#1d4ed8';
        if (d.type === 'class') return '#0e7490';
        if (d.type === 'function' || d.type === 'method') return '#047857';
        return '#6b7280';
      })
      .attr('stroke-width', d => d.isCenter ? 3 : 1.5);

    node.append('circle')
      .attr('r', d => (impactScores.get(d.id) || 0) * 2)
      .attr('fill', 'none')
      .attr('stroke', d => {
        const score = impactScores.get(d.id) || 0;
        if (score >= 7) return '#ef4444';
        if (score >= 4) return '#f59e0b';
        return '#10b981';
      })
      .attr('stroke-width', 1.5)
      .attr('stroke-dasharray', '3,3')
      .attr('stroke-opacity', 0.7);

    node.append('text')
      .attr('dx', 15)
      .attr('dy', 5)
      .text(d => d.name)
      .attr('font-size', d => d.isCenter ? '14px' : '12px')
      .attr('font-weight', d => d.isCenter ? 'bold' : 'normal');

    node.select('text')
      .each(function() {
        const textElement = this as SVGTextElement;
        const bbox = textElement.getBBox();
        const parent = textElement.parentNode as SVGGElement;
        d3.select(parent)
          .insert('rect', 'text')
          .attr('x', bbox.x - 2)
          .attr('y', bbox.y - 2)
          .attr('width', bbox.width + 4)
          .attr('height', bbox.height + 4)
          .attr('fill', 'white')
          .attr('fill-opacity', 0.8)
          .attr('rx', 3);
      });
    console.log('DEBUG - Node text and backgrounds added');

    node.append('title')
      .text(d => {
        const score = impactScores.get(d.id) || 0;
        return `${d.name} (${d.type})\nImpact Score: ${score.toFixed(1)}/10`;
      });

    simulation.on('tick', () => {
      allNodes.forEach(node => {
        node.x = Math.max(50, Math.min(width - 50, node.x || width / 2));
        node.y = Math.max(50, Math.min(height - 50, node.y || height / 2));
      });

      link.attr('d', d => {
        if (!d.source.x || !d.source.y || !d.target.x || !d.target.y) {
          console.error('DEBUG - Invalid link coordinates:', d);
          return '';
        }
        const dx = d.target.x - d.source.x;
        const dy = d.target.y - d.source.y;
        const dr = Math.sqrt(dx * dx + dy * dy) * 1.5;
        return `M${d.source.x},${d.source.y}A${dr},${dr} 0 0,1 ${d.target.x},${d.target.y}`;
      });

      node.attr('transform', d => {
        if (!d.x || !d.y) {
          console.error('DEBUG - Invalid node position:', d);
          return `translate(${width / 2},${height / 2})`;
        }
        return `translate(${d.x},${d.y})`;
      });
      console.log('DEBUG - Simulation tick executed');
    });

    const legend = svg.append('g')
      .attr('transform', `translate(10, 20)`);

    legend.append('text')
      .attr('x', 0)
      .attr('y', 0)
      .text('Impact Analysis')
      .attr('font-size', '16px')
      .attr('font-weight', 'bold');

    const legendItems = [
      { color: '#3b82f6', label: 'Current Node' },
      { color: '#ef4444', label: 'High Impact' },
      { color: '#f59e0b', label: 'Medium Impact' },
      { color: '#10b981', label: 'Low Impact' }
    ];

    legendItems.forEach((item, i) => {
      legend.append('rect')
        .attr('x', 0)
        .attr('y', 25 + i * 20)
        .attr('width', 15)
        .attr('height', 15)
        .attr('fill', item.color);

      legend.append('text')
        .attr('x', 25)
        .attr('y', 25 + i * 20 + 12)
        .text(item.label)
        .attr('font-size', '12px');
    });
    console.log('DEBUG - Legend added');

    function dragstarted(event: any, d: any) {
      if (!event.active) simulation.alphaTarget(0.3).restart();
      d.fx = d.x;
      d.fy = d.y;
      console.log('DEBUG - Drag started on node:', d.id);
    }

    function dragged(event: any, d: any) {
      d.fx = event.x;
      d.fy = event.y;
      console.log('DEBUG - Dragging node:', d.id, 'to', event.x, event.y);
    }

    function dragended(event: any, d: any) {
      if (!event.active) simulation.alphaTarget(0);
      d.fx = null;
      d.fy = null;
      console.log('DEBUG - Drag ended on node:', d.id);
    }

    console.log('DEBUG - useEffect completed');
  }, [nodeId, nodeName, nodeType, connections, hasIssues, highComplexity]);

  return (
    <div className="impact-analysis">
      <svg ref={svgRef}></svg>
      <div className="mt-4 text-sm text-gray-600">
        <p>This visualization shows the potential impact of changes to this component on other parts of the codebase.</p>
        <p>Larger dotted circles indicate higher potential impact. Nodes can be dragged to rearrange the visualization.</p>
      </div>
    </div>
  );
};

export default ImpactAnalysis;